const validateProductPrice = (productPrice: number | null) => {
  if (typeof productPrice !== "number") {
    throw new Error("Í∞íÏùÑ Î™®Îëê ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
  }

  if (productPrice < PRODUCT.MIN_PRICE || productPrice > PRODUCT.MAX_PRICE) {
    throw new Error(
      `ÏÉÅÌíà Í∞ÄÍ≤©ÏùÄ ${PRODUCT.MIN_PRICE}ÏõêÎ∂ÄÌÑ∞, ÏµúÎåÄ ${PRODUCT.MAX_PRICE}ÏõêÍπåÏßÄ Í∞ÄÎä•Ìï©ÎãàÎã§.`
    );
  }

  if (productPrice % PRODUCT.UNIT !== 0) {
    throw new Error(`ÏÉÅÌíà Í∞ÄÍ≤©ÏùÄ ${PRODUCT.UNIT}ÏõêÏúºÎ°ú ÎÇòÎàÑÏñ¥ Îñ®Ïñ¥Ï†∏ÏïºÌï©ÎãàÎã§.`);
  }
};

const valudateProductQuantity = (productQuantity: number | null) => {
  if (
    productQuantity > PRODUCT.MAX_QUANTITY ||
    productQuantity < PRODUCT.MIN_QUANTITY
  ) {
    throw new Error(
      `Ï†úÌíàÎãπ ÏàòÎüâÏùÄ ÏµúÏÜå ${PRODUCT.MAX_QUANTITY}Í∞úÎ∂ÄÌÑ∞ ÏµúÎåÄ ${PRODUCT.MIN_QUANTITY}Í∞úÍπåÏßÄ Í∞ÄÎä•Ìï©ÎãàÎã§.`
    );
  }

  if (productQuantity - Math.floor(productQuantity)) {
    throw new Error("Ï†úÌíàÏùò ÏàòÎüâÏùÄ ÏÜåÏàòÏ†êÏúºÎ°ú ÏûÖÎ†•Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
  }
};

