      `ÏµúÏÜå ${CHARGE.MIN_PRICE}Ïõê, ÏµúÎåÄ ${CHARGE.MAX_PRICE}ÏõêÍπåÏßÄ Ï∂©Ï†ÑÌï† Ïàò ÏûàÏäµÎãàÎã§.`
